import React, { Component } from "react";
import Axios from "axios";
import Button from "../Button";
import Performer from "./Performer";
import Dealer from "./Performer";           //Chaanges
import PerformerForm from "./PerformerForm";

class CarDealerContainer extends Component {
  constructor(props) {
    super(props);
    this.state = { dealers: [] };
  }

  getAllData = () => {
    Axios.get("http://localhost:8081/api/dealers")
      .then(res => {
        //console.log(res.data);
        this.setState({ dealers: res.data });
      })
      .catch(err => {
        console.log(err);
      });
  };

  onDeleteClickHandler = title => {
    console.log(title);
    Axios.delete("http://localhost:8081/api/dealers/" + title)
      .then(res => {
        console.log("Deleting: " + title);
        this.getAllData();
      })
      .catch(err => {
        console.log(err);
      });
  };

  showAllData = () => {
    let data = this.state.dealers.map((p, index) => {
      return (
        <Dealer
          key={p.title}
          index={index + 1}
          title={p.title}
          type={p.type}
          country={p.country}
          {...this.props}
        >
          <Button
            title="Delete"
            action={() => this.onDeleteClickHandler(p.title)}
            type="btn-warning"
          />
        </Dealer>
      );
    });
    return data;
  };

  componentDidMount = () => {
    this.getAllData();
  };

  render() {
    return (
      <div className="container">
        <div className="row my-3">
          <PerformerForm {...this.props} />
        </div>
        <div className="row">
          <table className="table table-striped table-dark">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">Title</th>
                <th scope="col">Type</th>
                <th scope="col">Country</th>
                <th scope="col">Town</th>
                <th scope="col">Name</th>
                <th scope="col">DOB</th>
                <th scope="col">Picture</th>
                <th scope="col">Actions</th>
              </tr>
            </thead>
            <tbody>{this.showAllData()}</tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default CarDealerContainer;
