import React from "react";
import { Route, BrowserRouter, Switch } from "react-router-dom";
import CarBrandContainer from "../Studio/CarBrandContainer";
//import StudioContainer from "../Studio/StudioContainer";
import Navigation from "./Navigation";
import StudioDetails from "../Studio/StudioDetails";
import PerformerContainer from "../Performer/PerformerContainer";
import CarDealerContainer from "../Performer/CarDealerContainer";
import CarContainer from "../Car/CarContainer";
import StudioUpdateForm from "../Studio/StudioUpdateForm";

const NavigationContainer = () => {
  return (
    <div>
      <BrowserRouter>
        <div>
          <Navigation />
          <Switch>
            <Route path="/" exact component={CarBrandContainer} />
            <Route path="/studios/:title" component={StudioDetails} exact />
            <Route path="/dealers" component={CarDealerContainer} exact />
            <Route path="/clients" component={CarContainer} exact />
            <Route
              path="/studios/update/:title"
              component={StudioUpdateForm}
              exact
            />
          </Switch>
        </div>
      </BrowserRouter>
    </div>
  );
};

export default NavigationContainer;
