import React, { Component } from "react";
import Axios from "axios";
import Button from "../Button";
import Car from "./Car";
import CarForm from "./CarForm";

class CarContainer extends Component {
  constructor(props) {
    super(props);
    this.state = { clients: [] };
  }

  getAllData = () => {
    Axios.get("http://localhost:8081/api/clients")
      .then(res => {
        //console.log(res.data);
        this.setState({ clients: res.data });
      })
      .catch(err => {
        console.log(err);
      });
  };

  onDeleteClickHandler = brand => {
    console.log(brand);
    Axios.delete("http://localhost:8081/api/clients/" + brand)
      .then(res => {
        console.log("Deleting: " + brand);
        this.getAllData();
      })
      .catch(err => {
        console.log(err);
      });
  };

  showAllData = () => {
    let data = this.state.clients.map((p, index) => {
      return (
        <Car
          key={p.brand}
          index={index + 1}
          brand={p.brand}
          fuel={p.fuel}
          price={p.price}
          {...this.props}
        >
          <Button
            title="Delete"
            action={() => this.onDeleteClickHandler(p.brand)}
            type="btn-warning"
          />
        </Car>
      );
    });
    return data;
  };

  componentDidMount = () => {
    this.getAllData();
  };

  render() {
    return (
      <div className="container">
        <div className="row my-3">
          <CarForm {...this.props} />
        </div>
        <div className="row">
          <table className="table table-striped table-dark">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">Brand</th>
                <th scope="col">Fuel</th>
                <th scope="col">Price</th>
                <th scope="col">Picture</th>
                <th scope="col">Country</th>
                <th scope="col">DOB</th>
                <th scope="col">Picture</th>
                <th scope="col">Actions</th>
              </tr>
            </thead>
            <tbody>{this.showAllData()}</tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default CarContainer;
