import React, { Component } from "react";
import Axios from "axios";
import Studio from "./Studio";
import Button from "../Button";
import StudioForm from "./StudioForm";
import NoPicture from "../../images/no_img.svg";

class CarBrandContainer extends Component {
  constructor(props) {
    super(props);
    this.state = { brands: [] };
  }

  getAllData = () => {
    Axios.get("http://localhost:8081/api/brands")
      .then(res => {
        console.log(res.data);
        this.setState({ brands: res.data });
      })
      .catch(err => {
        console.log(err);
      });
  };

  onDeleteClickHandler = title => {
    Axios.delete("http://localhost:8081/api/brands/" + title)
      .then(res => {
        console.log(res);
        this.getAllData();
      })
      .catch(err => {
        console.log(err);
      });
  };

  onDetailsClickHandler = title => {
    this.props.history.push("/brands/" + title);
  };

  showAllData = () => {
    let data = this.state.brands.map((s, index) => {
    //   let category;
    //   let img;
    //   if (s.logo === "null" || s.log === null) {
    //     img = NoPicture;
    //   } else {
    //     img = s.logo;
    //   }
    //   switch (s.category) {
    //     case 0:
    //       category = "National";
    //       break;
    //     case 1:
    //       category = "Home";
    //       break;
    //     case 2:
    //       category = "Live";
    //       break;
    //     default:
    //       break;
    //   }
      return (
        <Studio
          key={s.title}
          index={index + 1}
          title={s.title}
          logo={s.logo}
          success={s.success}
          {...this.props}
        >
          <Button
            title="Details"
            action={() => this.onDetailsClickHandler(s.title)}
            type="btn-success"
          />
          <Button
            title="Delete"
            action={() => this.onDeleteClickHandler(s.title)}
            type="btn-warning"
          />
        </Studio>
      );
    });
    return data;
  };

  componentDidMount() {
    this.getAllData();
  }

  render() {
    return (
      <div className="container">
        <div className="row">
          <StudioForm {...this.props} />
        </div>
        <div className="row">
          <table className="table table-striped table-dark">
            <thead>
              <tr>
                <th scope="col">#</th>
                <th scope="col">Title</th>
                <th scope="col">Logo</th>
                <th scope="col">Success</th>
                <th scope="col">Model2</th>
                <th scope="col">Information</th>
              </tr>
            </thead>
            <tbody>{this.showAllData()}</tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default CarBrandContainer;
